        ERROR_HANDLE_INTERRUPTS: 120008,
        ERROR_CALL_NATIVE_FUNCTION_EXCEPTION_CODE: 120009,
        ERROR_LOAD_GAME_TEXT: 120010,
        ERROR_LOAD_ANIMATION_SCRIPTS: 130002,

        ERROR_STARTING_ROUND: 130000,
        ERROR_ENDING_ROUND: 130003,
        ERROR_STARTING_SPLASH: 130001,
