        ERROR_MAX: 139999,

        WARN_MIN: 140000,
        WARN_CASHIER_FAIL: 140001,
        WARN_SWITCH_TO_MONEY_FAIL: 140002,
        WARN_CONTACT_US_FAIL: 140003,
