import './Logger.js';
const ERROR_CODES = window.log.ERROR_CODES;
const ERROR_MSG = window.log.ERROR_MSG;
(function init() {

    window.log.init();

    var gameFolderName = window.GameData.GID;
    if (window.EnvData.ADA && (window.EnvData.ADA === true)) {
        gameFolderName = 'ADA';
    }

    var DownloadServerGamesGameNumURL = window.EnvData.MDSURL + '/secure/HTML5Games/' + gameFolderName + '/' + window.HTML5GamesBuildNumber + '/game';

    window._URLS.vendorScriptURL    = DownloadServerGamesGameNumURL +'/vendors.bundle.js?'+ window.CACHE_BUSTER_PARAM + window.HTML5GamesCacheBusterNumber;
    window._URLS.gameScriptURL      = DownloadServerGamesGameNumURL +'/game.bundle.js?'+ window.CACHE_BUSTER_PARAM + window.HTML5GamesCacheBusterNumber;

    loadScript( window._URLS.vendorScriptURL, onVendorScriptLoaded, onVendorsScriptsLoadError);
}) ();

function onVendorScriptLoaded() {
    logger.log('------> VENDOR SCRIPTS LOADED');
    loadScript(window._URLS.gameScriptURL, onGameScriptLoaded, onGameScriptsLoadError);
}

function onGameScriptLoaded() {
    logger.log('------> GAME SCRIPTS LOADED');
}

function loadScript(src, callback, onError) {
    var script = document.createElement('script');
    script.type = 'text/javascript';
    script.src = src;
    script.onload = callback;
    script.onerror = onError;
    document.getElementsByTagName('head')[0].appendChild(script);
}

function onGameScriptsLoadError()
{
    var failCallback = window.NotifyGameLoadFailed || function (){};
    logger.log('------> ERROR LOADING GAME SCRIPTS');
    return failCallback(ERROR_CODES.ERR_INITIATE_THE_GAME, ERROR_MSG.ERR_INITIATE_THE_GAME_MSG, window.GameData.GID);
}

function onVendorsScriptsLoadError()
{
    var failCallback = window.NotifyGameLoadFailed || function (){};
    logger.log('------> ERROR LOADING VENDOR SCRIPTS');
    return failCallback(ERROR_CODES.ERR_INITIATE_THE_GAME, ERROR_MSG.ERR_INITIATE_THE_GAME_MSG, window.GameData.GID);
}
/* eslint-enable */


